<template>
  <el-form class="form" :inline="true" @submit.native.prevent="onSubmit" :model="form" label-width="100px">
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/business/list">商务合作</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="姓名">
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="电话">
          <el-input v-model="form.tel" placeholder="请输入电话"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="邮箱">
          <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="排序">
          <el-input v-model="form.sort" placeholder="请输入排序"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="是否可以入驻">
          <el-select size="small" v-model="form.status" clearable placeholder="请选择">
            <el-option value="Y" label="是" />
            <el-option value="N" label="否" />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-form-item label="封面" prop="thumb">
          <el-upload
            :limit="1"
            :action="'/api/api/upload?ssid=' + ssid"
            :on-success="fileSuccess1"
            :file-list="file"
            list-type="picture"
            :on-remove="onDelct1"
          >
            <el-button size="small" type="infor">点击上传</el-button>
          </el-upload>
        </el-form-item>
      </el-col>
      <!-- <el-col>
        <el-form-item label="内容" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @imageAdded="handleImageAdded"
            v-model="form.content"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
      </el-col> -->
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { VueEditor } from 'vue2-editor'
import { Message } from 'element-ui'

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      config: {
        cats: {},
      },
      file: [],
      form: {
        email: '',
        name: '',
        status: '',
        tel: '',
        thumb: '',
        sort: 0,
      },
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    const id = this.$router.history.current.params.id

    if (id > 0) {
      Fetch('/article/businesscoo/view', { id }).then(r => {
        this.file = [{ url: r.data.view.thumb, name: r.data.view.thumb }]
        this.form = r.data.view
      })
    }
  },
  methods: {
    fileSuccess1(res, file) {
      this.file = [file]
      this.form.thumb = res.data.file
    },
    onDelct1(file, fileList) {
      this.file1 = fileList
      this.form.thumb = fileList.map(item => item.name)
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      console.log(formData)
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      this.form.status_name = this.form.status === 'Y' ? '开启' : '关闭'
      Fetch('/article/businesscoo/' + (id ? 'update' : 'create'), {
        id: this.$router.history.current.params.id,
        ...this.form,
      }).then(() => {
        Message.success('操作成功')
        this.$router.replace('/business/list')
      })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
